
import dateFormat from 'vux/src/tools/date/format.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import selectCity from '@/components/selectCity'
import {customer, customerEdit, customerDel} from '../config'
export default {
  components: {
    dateFormat,
    Group,
    Cell,
    XInput,
    XButton,
    XTextarea,
    selectCity
  },
  data () {
    return {
      edit: false,
      loading: false,
      data: [],
      userId: 'da738f8b730b4b62be0333cec2b9dd1b',
      form: {
        userId: this.$route.params.userId,
        customerId: this.$route.params.customerId,
        customer: {}
      }
    }
  },
  computed: {
    text () {
      if (this.edit) {
        return '完成'
      } else {
        return '编辑'
      }
    }
  },
  created () {
    setTimeout(() => {
      this.handleInfo()
    }, 100)
  },
  methods: {
    handleDel () {
      this.loading = true
      this.$http({
        method: 'jsonp',
        url: customerDel,
        jsonp: 'callback',
        params: {
          userId: this.form.userId,
          customerId: this.form.customerId
        },
        jsonpCallback: 'json'
      })
      .then(res => {
        console.log(res)
        this.loading = false
        if (res.body.status) {
          this.$vux.toast.show({
            type: 'text',
            width: '15em',
            position: 'bottom',
            text: '用户删除成功！',
            time: '3000'
          })
        }
      })
    },
    handleSave () {
      this.edit = !this.edit
      if (!this.edit) {
        this.form.customer.areaId = 101100
        this.form.customer = JSON.stringify(this.form.customer)
        this.handleEdit()
      }
    },
    handleEdit () {
      this.$http({
        method: 'jsonp',
        url: customerEdit,
        jsonp: 'callback',
        params: this.form,
        jsonpCallback: 'json'
      })
      .then(res => {
        if (res.body.status) {
          this.form.customer = JSON.parse(this.form.customer)
          this.$vux.toast.show({
            type: 'text',
            width: '15em',
            position: 'bottom',
            text: '用户资料修改成功！',
            time: '3000'
          })
        }
      })
    },
    handleInfo () {
      this.$http({
        method: 'jsonp',
        url: customer,
        jsonp: 'callback',
        params: this.form,
        jsonpCallback: 'json'
      })
      .then(res => {
        this.form.customer = res.body.data.customer
        console.log(res.body.data.customer)
      })
    },
    formatDate (date) {
      return dateFormat(date, 'YYYY-MM-DD')
    }
  }
}



